  width: 32px;
  height: 32px;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 50%;
  margin: 0 auto;
  transition: background-color .5s cubic-bezier(.65, .05, .36, 1), box-shadow .5s cubic-bezier(.65, .05, .36, 1);
  display: block;
}

