  box-shadow: 0 0 0 5px rgba(79, 46, 232, .3);
}

.support-btn-icon {
  width: 12px;
  height: 6px;
  fill: #4f2ee8;
  margin: 0 auto;
  transition: fill .5s cubic-bezier(.65, .05, .36, 1);
}

.support-btn-icon:hover, .support-btn-icon:focus {
  fill: #fff;
}

